.App {
  text-align: center;
  padding: 20px;
  background-size: cover;
  background-position: center;
}

.Customization {
  text-align: left;
  max-width: 300px;
  margin: 0 auto;
}

.Customization label {
  display: block;
  margin: 10px 0;
}

input {
  padding: 10px;
  font-size: 16px;
  width: 200px;
  margin-right: 10px;
}

button {
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left;
}

li span {
  flex-grow: 1;
  text-align: left;
  cursor: pointer;
  padding: 10px;
  background-size: cover;
  background-position: center;
}

.completed {
  text-decoration: line-through;
  color: gray;
}
